const gettime = {
	getChatTime(v1, v2) {
		v1 = v1.toString().length < 13 ? v1 * 1000 : v1;
		v2 = v2.toString().length < 13 ? v2 * 1000 : v2;
		if (((parseInt(v1) - parseInt(v2)) / 1000) > 300) {
			return this.gettime(v1)
		}
	},
	gettime(shorttime) {
		shorttime = shorttime.toString().length < 13 ? shorttime * 1000 : shorttime
		let now = (new Date()).getTime()
		let cha = (now - parseInt(shorttime)) / 1000
		if (cha < 43200) {
			return this.dateFormat(new Date(shorttime), "{A} {t}:{ii}")
		} else if (cha < 518400) {
			return this.dateFormat(new Date(shorttime), "{Mon}{DD} {A} {t}:{ii}")
		} else {
			return this.dateFormat(new Date(shorttime), "{Y}-{MM}-{DD} {A} {t}:{ii}")
		}
	},
	parseNumber(num) {
		return num < 10 ? "0" + num : num
	},
	dateFormat(date, formatStr) {
		let dateObj = {}
		let rStr = /\{([^}]+)\}/
		let mons = ['', '', '', '', '', '', '', '', '', '', '', '']
		dateObj['Y'] = date.getFullYear()
		dateObj['M'] = date.getMonth() + 1
		dateObj['MM'] = this.parseNumber(dateObj['M'])
		dateObj['Mon'] = mons[dateObj['M'] - 1]
		dateObj['D'] = date.getDate()
		dateObj['DD'] = this.parseNumber(dateObj['D'])
		dateObj['h'] = date.getHours()
		dateObj['hh'] = this.parseNumber(dateObj['h'])
		dateObj['t'] = dateObj['h'] > 12 ? dateObj['h'] - 12 : dateObj['h']
		dateObj['tt'] = this.parseNumber(dateObj['t'])
		dateObj['A'] = dateObj['h'] > 12 ? '' : '',
			dateObj['i'] = date.getMinutes()
		dateObj['ii'] = this.parseNumber(dateObj['i'])
		dateObj['s'] = date.getSeconds()
		dateObj['ss'] = this.parseNumber(dateObj['ss'])

		while (rStr.test(formatStr)) {
			formatStr = formatStr.replace(rStr, dateObj[RegExp.$1])
		}
		return formatStr
	},
	getAgeByBirthday(data) {
		let birthday = new Date(data.replace(/-/g, "\/"));
		let d = new Date();
		return d.getFullYear() - birthday.getFullYear() - ((d.getMonth() < birthday.getMonth() || d.getMonth() == birthday.getMonth() &&
			d.getDate() < birthday.getDate()) ? 1 : 0);
	}
}
export default {
	gettime
}
